header {
  margin: 0;
  background-color: transparent;
  color: white;
  max-height: 150px;
  display: flex;
  justify-content: center;

  @media screen and (max-width: 600px) {
    justify-content: flex-start;
    max-height: 100%;
  }
}

.mb-container {
  display: flex;
  width: 100%;

  .mb-menu {
    display: none;

    div {
      background-color: red;
      margin-right: 20px;
    }

    @media screen and (max-width: 600px) {
      display: flex;
      justify-content: flex-end;
      width: 100%;
      position: fixed;
      z-index: 9999;
    }
  }
  .mb-menu-dropdown {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    height: 100vh;
    position: fixed;
    justify-content: center;
    background: black;
    overflow-x: hidden;
    overflow-y: hidden;
    z-index: 999;
    overflow-y: hidden;

    ul {
      list-style: none;
      padding: 0;
      li {
        a {
          color: #b62b2b;
          text-align: justify;
        }
      }
    }

    @media screen and (min-width: 600px) {
      display: none;
    }
  }
}

.logo-header {
  max-width: 100px;
  max-height: 100px;
  margin-right: 150px;

  @media screen and (max-width: 1000px) {
    margin-right: 50px;
  }

  @media screen and (max-width: 700px) {
    margin-right: 20px;
    max-height: 70px;
    max-width: 70px;
  }

  @media screen and (max-width: 600px) {
    display: none;
  }

  img {
    width: 100%;
    height: 100%;
  }
}

.inner-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  z-index: 1000;
  position: fixed;

  @media screen and (max-width: 700px) {
    margin-left: 40px;
  }

  @media screen and (max-width: 600px) {
    display: none;
  }

  h1 {
    font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
      "Lucida Sans Unicode", Geneva, Verdana, sans-serif, sans-serif;
    font-size: 5rem;
  }

  ul {
    display: flex;
    max-width: 900px;
    width: 100%;
    padding-left: 0;

    li {
      list-style: none;
      margin-left: 2rem;

      a {
        text-decoration: none;
        color: white;
        margin-right: 20px;
        transition: 0.3s ease;
        font-family: "Bebas Neue";
        text-transform: uppercase;

        &:hover {
          color: lightgrey;
        }
      }
    }
  }

  @media screen and (max-width: 700px) {
    a {
      font-size: 1.7rem;
    }
    ul {
      li {
        margin-left: 0.2rem;
      }
    }
  }
}

.navbar__link--active {
  box-shadow: 0 4px 0px #b62b2b;
  transition: border-bottom 0.5s ease-in-out;
  color: darkgrey !important;
}
